.gpt3__cta {
    border: 1px solid white;
    color: white;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.gpt3__cta-content p {
    color: #0E0E0E;
    font-size: 12px;
    font-family: var(--font-family);
    font-weight: 500;
    line-height: 30px;
}

.gpt3__cta-content h3 {
    color: #000;
    font-size: 24px;
    font-family: var(--font-family);
    font-weight: 800;
    line-height: 45px;
}

.gpt3__cta-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-button button {
    border-radius: 40px;
    background: #000;
    width: 190px;
    height: 60px;

    color: #FFF;
    font-size: 18px;
    font-family: var(--font-family);
    font-weight: bold;
    line-height: 30px;
}

@media screen and (max-width: 650px) {
    .gpt3__cta {
        flex-direction: column;
    }
    
    .gpt3__cta-button {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta {
        margin: 4rem 2rem;
    }

    .gpt3__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .gpt3__cta-button button {
        font-size: 14px;
        line-height: 28px;
    }
}